html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Jost", sans-serif;
  font-weight: 500;
  color: var(--color-dark);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  font-weight: inherit;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1820px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.main-link {
  position: relative;
  font-size: 15px;
  color: var(--color-dark);
}

.main-link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 0%;
  height: 2px;
  background-color: currentColor;
  transition: width 0.15s linear;
}

.main-link_white {
  color: var(--color-white);
}

.main-link_current {
  cursor: pointer;
}

.main-link_current::before {
  width: 100%;
  transition: width 0.15s linear;
}

.main-link:hover::before {
  width: 100%;
  transition: width 0.15s linear;
}

.container-narrow {
  max-width: 1326px;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 36px;
  padding-bottom: 36px;
}

.header__shop-nav {
  margin-left: 250px;
}

.logo {
  display: inline-block;
  width: 117px;
  height: 22px;
}

.logo svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--color-dark);
  object-fit: cover;
}

.catalog-header {
  padding-top: 40px;
}

.free-delivery {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 120;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40px;
  font-size: 10px;
  text-transform: uppercase;
  color: var(--color-white);
  background-color: #111;
}

.free-delivery__btn {
  position: absolute;
  right: 40px;
  top: 50%;
  fill: var(--color-white);
  transform: translateY(-50%);
}

.free-delivery__btn svg {
  width: 12px;
  height: 12px;
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 46px;
}

.shop-nav {
  display: flex;
  align-items: center;
}

.shop-nav__item:not(:last-child) {
  margin-right: 25px;
}

.shop-nav__link {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.shop-nav__link_user {
  background-image: url("../img/user.png");
}

.cart {
  display: inline-flex;
  align-items: center;
  width: auto;
  height: auto;
}

.cart__icon {
  display: inline-block;
  margin-right: 4px;
  width: 16px;
  height: 16px;
  background-image: url("../img/cart.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.quantity {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  padding: 0 5px;
  width: 16px;
  height: 16px;
  font-weight: 700;
  font-size: 10px;
  color: var(--color-white);
  background-color: var(--color-dark);
}

.search-form__btn {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-image: url("../img/search.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero {
  height: 100vh;
  min-height: 700px;
  background-color: var(--quil-gray);
}

.hero::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #000;
  opacity: 0.05;
}

.hero__container {
  position: relative;
  height: 100%;
}

.hero__title {
  margin: 0;
  margin-bottom: 40px;
  max-width: 540px;
  font-weight: 700;
  font-size: 72px;
}

.hero__descr {
  margin: 0;
  font-size: 18px;
}

.hero__image {
  position: absolute;
  right: 270px;
  bottom: 0;
  max-height: 90%;
}

.hero__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 260px;
  height: 100%;
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.banner-slider .swiper-slide {
  height: 100%;
}

.banner-pag {
  left: 50%;
  bottom: 46px;
}

.banner-pag .swiper-pagination-bullet {
  margin: 0 12px;
  width: 6px;
  height: 6px;
  background-color: var(--color-white);
  opacity: 0.5;
}

.banner-pag .swiper-pagination-bullet-active {
  position: relative;
  background-color: var(--color-dark);
  opacity: 1;
}

.banner-pag .swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: -1;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  background-color: var(--color-white);
  transform: translate(-50%, -50%);
}

.purched {
  position: absolute;
  left: 0;
  bottom: 50px;
  z-index: 1;
  display: flex;
  padding: 10px;
  width: 280px;
  height: 113px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
  transform: translateX(-150vh);
  transition: transform 0.3s;
}

.purched_visible {
  transform: translateX(0);
}

.purched__image {
  margin-right: 10px;
}

.purched__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.purched__descr {
  display: block;
  margin-bottom: 7px;
  font-size: 11px;
  color: var(--color-gray);
}

.purched__title {
  margin: 0;
  margin-right: 20px;
  margin-bottom: 7px;
  height: 34px;
  font-size: 15px;
}

.purched__when-from {
  display: block;
  padding-right: 40px;
  font-size: 11px;
  color: var(--color-gray);
}

.purched__close {
  position: absolute;
  right: 10px;
  top: 10px;
  padding: 0;
  width: 10px;
  height: 10px;
  background-image: url("../img/close.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.banners__container {
  padding-top: 50px;
  padding-bottom: 30px;
}

.banners__list {
  display: flex;
}

.banners__item {
  flex-shrink: 0;
  width: 33%;
}

.banners__item:not(:last-child) {
  margin-right: 0.6%;
}

.banners-article {
  padding: 55px 60px;
  padding-right: 45%;
  min-height: 290px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #eeeeec;
}

.banners-article_left {
  background-image: url("../img/midseason.jpg");
}

.banners-article_center {
  background-image: url("../img/summer.jpg");
}

.banners-article_right {
  background-image: url("../img/accs.jpg");
}

.banners-article__title {
  margin: 0;
  margin-bottom: 20px;
  font-size: 36px;
}

.main-products__container {
  padding-top: 55px;
  padding-bottom: 55px;
}

.main-products__nav {
  display: flex;
  justify-content: center;
  margin-bottom: 45px;
}

.main-products__list {
  margin-bottom: 70px;
}

.main-products__btn {
  font-size: 24px;
}

.main-products__item {
  margin: 0 20px;
}

.main-products__btn-center {
  text-align: center;
}

.main-products__more {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  font-size: 13px;
  color: var(--color-gray);
}

.main-products__more svg {
  margin-top: 12px;
  width: 12px;
  height: 12px;
  fill: var(--color-dark);
}

.products-grid {
  display: grid;
  justify-items: center;
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fill, minmax(330px, 1fr));
}

.products-grid__item {
  max-width: 340px;
}

.product {
  position: relative;
  max-width: 340px;
}

.product__image {
  position: relative;
  margin-bottom: 20px;
}

.product__image img {
  max-width: 100%;
  object-fit: cover;
}

.product__sale {
  position: absolute;
  left: 20px;
  top: 20px;
  border: 2px solid var(--color-dark);
  padding: 7px 15px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  color: #111;
}

.product__prop {
  display: inline-block;
  margin-bottom: 10px;
  padding-left: 15px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  color: #111;
  background-position: left center;
  background-repeat: no-repeat;
}

.product__title {
  margin: 0;
  margin-bottom: 11px;
  font-size: 13px;
  color: var(--color-tundora);
}

.product__title a::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.product__price {
  font-weight: 700;
  font-size: 13px;
  color: var(--color-dark);
}

.product__oldprice {
  margin-left: 5px;
  text-decoration: line-through;
  color: var(--color-gray);
}

.new {
  background-image: url("../img/new.png");
}

.best {
  background-image: url("../img/best.png");
}

.shop-banners {
  padding-top: 45px;
}

.shop-banners__container {
  display: flex;
  justify-content: space-between;
  padding: 0;
  max-width: 1920px;
}

.shop-banner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  width: 50%;
  min-height: 420px;
  color: var(--color-white);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--color-dark);
}

.shop-banner::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(215, 8, 8, 0.1);
}

.shop-banner_left {
  background-image: url(../img/banner-1.jpg);
}

.shop-banner_right {
  background-image: url(../img/banner-2.jpg);
}

.shop-banner__title {
  position: relative;
  z-index: 1;
  margin: 0;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 48px;
}

.shop-banner__text {
  position: relative;
  z-index: 1;
}

.hero-catalog {
  padding-top: 130px;
}

.hero-catalog__slider {
  position: relative;
  overflow: hidden;
  width: 100%;
  background-color: #f8f8f8;
}

.hero-catalog__container {
  position: relative;
}

.catalog-slide {
  padding: 108px;
  min-height: 245px;
}

.catalog-slide__title {
  margin: 0;
  margin-bottom: 21px;
  max-width: 375px;
  font-weight: 700;
  font-size: 36px;
}

.catalog-slide__descr {
  margin: 0;
  margin-bottom: 46px;
  max-width: 390px;
  font-weight: 500;
  font-size: 15px;
  color: var(--color-tundora);
}

.catalog-slide__btns {
  display: flex;
  align-items: center;
}

.catalog-slide__image {
  position: absolute;
  right: 19%;
  bottom: 0;
}

.catalog-slide__link {
  display: inline-block;
  padding: 18px 45px;
  font-weight: 500;
  font-size: 13px;
  color: var(--color-white);
  background-color: var(--color-dark);
}

.catalog-slide__link_light {
  border: 1px solid var(--color-dark);
  color: var(--color-dark);
  background-color: transparent;
}

.catalog-slide__link:not(:last-child) {
  margin-right: 15px;
}

.hero-prev-btn,
.hero-next-btn {
  position: absolute;
  left: 30px;
  top: 50%;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--color-dark);
  width: 48px;
  height: 48px;
  color: var(--color-dark);
  transform: translateY(-50%);
  transition: background-color 0.3s;
}

.hero-prev-btn svg,
.hero-next-btn svg {
  width: 40px;
  height: 20px;
  fill: currentColor;
  transition: fill 0.3s;
}

.hero-prev-btn:hover,
.hero-next-btn:hover {
  background-color: var(--color-dark);
}

.hero-prev-btn:hover svg,
.hero-next-btn:hover svg {
  fill: var(--color-white);
}

.hero-next-btn {
  left: auto;
  right: 30px;
}

.breadcrumbs__list {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #eee;
  padding-top: 35px;
  padding-bottom: 30px;
}

.breadcrumbs__item:not(:last-child) {
  position: relative;
  margin-right: 35px;
}

.breadcrumbs__item:not(:last-child)::after {
  content: "/";
  position: absolute;
  right: -20px;
  top: 50%;
  color: var(--color-gray);
  transform: translateY(-50%);
}

.breadcrumbs__link {
  font-size: 10px;
  text-transform: uppercase;
  color: var(--color-gray);
}

.breadcrumbs__link_current {
  color: var(--color-dark);
}

.catalog-filters {
  margin-right: 30px;
  padding-top: 27px;
  padding-right: 66px;
  width: 309px;
}

.catalog-content {
  min-height: 500px;
}

.catalog-content .container {
  display: flex;
}

.hide-filters {
  margin-bottom: 18px;
  padding-left: 20px;
  font-weight: 700;
  font-size: 10px;
  color: var(--color-dark);
  background-image: url("../img/cross.png");
  background-position: left center;
  background-repeat: no-repeat;
}

.catalog-filter {
  border-top: 1px solid #eee;
}

.catalog-filter__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 39px;
  padding-bottom: 39px;
  cursor: pointer;
  user-select: none;
}

.catalog-filter__caption {
  display: flex;
  align-items: center;
}

.catalog-filter__title {
  display: inline-block;
  margin: 0;
  font-weight: 700;
  font-size: 13px;
  text-transform: uppercase;
}

.catalog-filter__quantity {
  margin-left: 5px;
  transform: translateY(-1px);
}

.catalog-filter__bottom {
  display: none;
  padding-bottom: 40px;
}

.catalog-filter__toggle {
  position: relative;
  width: 12px;
  height: 12px;
  color: #aaa;
}

.catalog-filter__toggle::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  width: 2px;
  height: 100%;
  background-color: currentColor;
  transform: translateX(-50%);
}

.catalog-filter__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  transform: translateY(-50%);
}

.catalog-filter__items {
  columns: 2;
}

.catalog-filter__item {
  margin-bottom: 14px;
}

.catalog-filter__item:nth-child(5n) {
  margin-bottom: 0;
}

.catalog-filter_open .catalog-filter__toggle::before {
  display: none;
}

.catalog-filter_open .catalog-filter__bottom {
  display: block;
}

.catalog-filter:last-child {
  border-bottom: 1px solid #eee;
}

.custom-checkbox {
  display: inline-block;
  cursor: pointer;
  user-select: none;
}

.custom-checkbox__text {
  position: relative;
  padding-left: 20px;
  font-weight: 500;
  font-size: 13px;
  color: var(--color-gray);
}

.custom-checkbox__text::before {
  content: "";
  position: absolute;
  left: 0;
  top: 3px;
  border: 2px solid #aaa;
  width: 11px;
  height: 11px;
}

.custom-checkbox__text::after {
  content: "";
  position: absolute;
  left: 2px;
  top: 0;
}

.custom-checkbox__input:checked + span {
  color: var(--color-dark);
}

.custom-checkbox__input:checked + span::before {
  border-color: var(--color-dark);
}

.custom-checkbox__input:checked + span::after {
  content: "\2713";
}

.catalog-grid {
  padding-top: 35px;
  width: 987px;
}

.catalog-grid__content {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 70px;
  padding-top: 20px;
}

.catalog-grid__item {
  margin-bottom: 70px;
}

[data-grid-columns="5"] .catalog-grid__item {
  width: 17.6%;
}

[data-grid-columns="5"] .catalog-grid__item:not(:nth-child(5n)) {
  margin-right: 3%;
}

[data-grid-columns="5"] .catalog-grid__item:nth-last-child(-n + 5) {
  margin-bottom: 0;
}

[data-grid-columns="4"] .catalog-grid__item {
  width: 22.7%;
}

[data-grid-columns="4"] .catalog-grid__item:not(:nth-child(4n)) {
  margin-right: 3%;
}

[data-grid-columns="4"] .catalog-grid__item:nth-last-child(-n + 4) {
  margin-bottom: 0;
}

[data-grid-columns="3"] .catalog-grid__item {
  width: 31.3%;
}

[data-grid-columns="3"] .catalog-grid__item:not(:nth-child(3n)) {
  margin-right: 3%;
}

[data-grid-columns="3"] .catalog-grid__item:nth-last-child(-n + 3) {
  margin-bottom: 0;
}

.catalog-props__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.catalog-columns {
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
}

.catalog-columns__text {
  margin-right: 3px;
}

.catalog-columns__list {
  display: flex;
  align-items: center;
}

.catalog-columns__item:not(:last-child) {
  margin-right: 5px;
}

.catalog-columns__btn {
  padding: 0 5px;
  font-size: 10px;
  color: var(--color-gray);
}

.catalog-columns__btn_current {
  color: var(--color-dark);
}

.catalog-columns__btn:hover {
  color: var(--color-dark);
}

.catalog-choice {
  display: none;
  padding-top: 20px;
  font-size: 0;
}

.catalog-choice__item {
  margin-right: 5px;
  margin-bottom: 5px;
  border-radius: 13px;
  padding: 5px 14px;
  font-size: 13px;
  color: var(--color-tundora);
  background-color: #eee;
  transition: background-color 0.3s, color 0.3s;
}

.catalog-choice__item svg {
  margin-left: 5px;
  width: 9px;
  height: 9px;
  fill: currentColor;
  pointer-events: none;
}

.catalog-choice__item:hover {
  color: var(--color-white);
  background-color: var(--color-dark);
}

.catalog-choice__clear {
  margin-left: 5px;
}

.custom-select {
  position: relative;
  min-width: 115px;
}

.custom-select__dropdown {
  position: absolute;
  left: 0;
  top: calc(100% + 2px);
  z-index: 10;
  display: none;
  padding: 9px;
  width: 100%;
  background-color: var(--color-white);
}

.custom-select__item {
  transition: color 0.3s;
  cursor: pointer;
}

.custom-select__item:not(:last-child) {
  margin-bottom: 10px;
}

.custom-select__item:hover {
  color: var(--color-tundora);
}

.custom-select__top {
  position: relative;
  border-bottom: 1px solid #e5e5e7;
  padding-right: 30px;
  padding-bottom: 12px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  cursor: pointer;
  user-select: none;
}

.custom-select__top::after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -6px;
  width: 10px;
  height: 6px;
  background-image: url("../img/angle-down.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transform: translateY(-50%);
}

.custom-select_open .custom-select__top:after {
  transform: translateY(-50%) rotate(180deg);
}

.custom-select_open .custom-select__dropdown {
  display: block;
}

.custom-select_footer {
  min-width: auto;
}

.custom-select_footer .custom-select__top {
  border-bottom: none;
  padding-right: 13px;
  padding-bottom: 0;
  font-size: 13px;
}

.custom-select_footer .custom-select__top::after {
  margin-top: 0;
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagination__item {
  position: relative;
  border: 2px solid #eee;
  padding: 16px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  color: var(--color-gray);
  transition: color 0.3s, background-color 0.3s, border 0.3s;
}

.pagination__item_current {
  border: 2px solid var(--color-dark);
  color: var(--color-white);
  background-color: var(--color-dark);
}

.pagination__item a {
  display: flex;
  align-items: center;
}

.pagination__item a::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.pagination__item svg {
  margin-left: 5px;
  width: 7px;
  height: 15px;
  fill: currentColor;
}

.pagination__item:not(:last-child) {
  margin-right: 5px;
}

.pagination__item:hover {
  border: 2px solid var(--color-dark);
  color: var(--color-white);
  background-color: var(--color-dark);
}

.pagination__item:last-child {
  padding: 14px 23px;
}

